canvas {
  width: 700px;
  height: calc(100vh - 90px);
  top: 80px;
  position: absolute;
}

.code-heading {
  font-family: sans-serif;
  font-size: 28px;
  text-transform: uppercase;
  display: inline-block;
  margin: 0;
  padding-right: 8px;
  vertical-align: middle;
}

.code {
  vertical-align: middle;
  display: inline-block;
  font-family: sans-serif;
  background: black;
  color: white;
  padding: 5px;
  letter-spacing: 15px;
  text-indent: 15px;
  height: 50px
}

.code .code-item {
  top: 8px;
  font-size: 35px;
  position: relative;
  line-height: 100%;
}

.code .asterisk {
  top: 15px;
}

.state-text {
  position: fixed;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
  font-family: 'helvetica';
  font-size: 40px;
  width: calc(700px - 70px);
  margin-left: calc(70px / 2);
  border-radius: 3px;
  padding: 16px;
  box-sizing: border-box;
  line-height: 1.5em
}

.state-text.hidden {
  display: none;
}

.state-text .heading {
  text-transform: uppercase;
  display: block;
  font-size: 110%;
}



